import { KeyMap } from "./KeyMap";
import { TypeConverter } from "./TypeConverter";

/**
 * 属性实体
 */
export class PropertyEntity {
    /**
     * 属性ID
     */
    propertyID: string;
    /**
     * 属性显示的名称
     */
    propertyName: string;
    /**
     *  属性的类型：String/Number/Boolean或自定义类型
     */
    propertyType: "string" | "number" | "boolean" | "select" | "modal" | "multiLanguage" | string = "string"; //
    /**
     * 属性描述
     */
    description?: string;
    /**
     * 属性的默认值
     */
    defaultValue?: any;
    /**
     * 是否只读
     */
    readonly?: boolean = false;
    /**
     * 是否可见
     */
    visible?: boolean = true;
    /**
     * 下拉框的枚举值
     */
    iterator?: KeyMap[];
    /**
     * 属性自定义编辑器（以模态框的形式展示）
     */
    editor?: any;
    /**
     * 属性值转换器
     */
    converter?: TypeConverter;
}